import './HomePage.css';
import Header from '../Header/Header';
import NewsFeed from './NewsFeed.js';
import Stats from './Stats.js';
import { useState, useRef } from 'react';

function HomePage() {
  const [currentStock, setCurrentStock] = useState('TSLA');
  const [showList, setShowList] = useState(false);
  const openList = () => {
    setShowList(prev => !prev)
  }
  const fieldRef = useRef();
  const ChangeStock = (symbol) => {
    setCurrentStock(symbol);
  }
  console.log(window.innerWidth)
  return (
    <div className="App">
      <div className="app__header">
        <Header ChangeStock={ChangeStock} currentStock={currentStock} openList={openList} showList={showList} />
      </div>
      <div className="app__body">
        {
          (window.innerWidth < 600 ?
            (showList ?
              <div className="stats">
                <div className="Stats__container">
                  <Stats currentStock={currentStock} ChangeStock={ChangeStock} openList={openList} showList={showList} />
                </div>
              </div>
              :
              <div />)
            :
            <div className="stats">
              <div className="Stats__container">
                <Stats currentStock={currentStock} ChangeStock={ChangeStock} openList={openList} showList={showList} />
              </div>
            </div>)
        }
        < div className="newsFeed" ref={fieldRef}>
          <div className="NewsFeed__container">
            <NewsFeed currentStock={currentStock} ChangeStock={ChangeStock} fieldRef={fieldRef} />
          </div>
        </div>
      </div>
    </div >
  );
}

export default HomePage;
