import Chart from 'react-apexcharts';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Info from './info';



export default function LineChart(props) {
  const [test, setTest] = useState("");
  useEffect(() => {
    const OHCLtest = (stock) => {
      const current_date = Math.floor(Date.now() / 1000);
      const prev_date = current_date - 31556952;
      const BASE_URL = 'https://finnhub.io/api/v1/stock/candle?symbol=';
      const Date_URL = `&resolution=D&from=${prev_date}&to=${current_date}`;
      const KEY_URL = '&token=c58valiad3idb0atsp6g';
      return axios
        .get(`${BASE_URL}${stock}${Date_URL}${KEY_URL}`)
        .catch((error) => {
          console.error("Error", error.message);
        });
    };
    OHCLtest(props.currentStock).then(res => { setTest(res.data); })
  }, [props.currentStock]);
  const series = [
    {
      name: "close",
      data: test["c"],
    }
  ];
  const options = {
    chart: {
      id: "chart1",
      type: "area",
      foreColor: "#ccc",
      toolbar: {
        show: false,
      }
    },
    colors: [props.redOrGreen[0]],
    fill: {
      type: "gradient",
      theme: "dark",
      gradient: {
        shadeIntensity: 1,
        opacityFrom: 0.7,
        opacityTo: 0.9,
        colorStops: [
          {
            offset: 0,
            color: props.redOrGreen[1],
            opacity: 0.7
          },
          {
            offset: 60,
            color: "black",
            opacity: 1
          },
        ]
      }
    },
    stroke: {
      width: 2,
      curve: 'straight',
    },
    tooltip: {
      theme: "dark"
    },
    grid: {
      borderColor: "gray",
      clipMarkers: false,
      yaxis: {
        lines: {
          show: false
        }
      }
    },
    dataLabels: {
      enabled: false
    },
    xaxis: {
      time: test["t"],
      tickAmount: 1,
      labels: {
        show: false,
      }
    },
    yAxes: [{
      display: true,
      ticks: {
        beginAtZero: true,
        steps: 10,
        stepValue: 5,
        max: 100
      }
    }]
  };
  return (
    <>
      <div className="chart__container">
        <Chart options={options} type="area" series={series} className="chart" padding="0px" height="300px" />
        <Info currentStock={props.currentStock} />
      </div>
    </>
  );
}
