import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { db } from './firebase';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';

function WatchList(props) {
  const [stocksData, setStocksData] = useState([]);
  const getStocksData = (stock) => {
    const BASE_URL = 'https://cloud.iexapis.com/stable/stock/';
    const KEY_URL = '/quote?token=pk_8ba887922f7c4eaa84ff14bcf63edcc6 ';
    //pk_4cd102e04d5447289c91ec8bff90a56e
    return axios
      .get(`${BASE_URL}${stock}${KEY_URL}`)
      .catch((error) => {
        console.error("Error", error.message);
      });
  };
  const getMyStocks = () => {
    db
      .collection('MyWatchList')
      .onSnapshot(snapshot => {
        let promises = [];
        let tempData = [];
        snapshot.docs.map((doc) => {
          let temp = ({ name: doc.data().StockName })
          promises.push(getStocksData(temp.name)
            .then(res => {
              tempData.push({
                name: temp.name,
                ...res.data
              })
            })
          )
        })
        Promise.all(promises).then(() => {
          setStocksData(tempData);
        })
      })
  }
  useEffect(() => {
    getMyStocks();
  }, []);
  return (
    stocksData.map((listitem, i) => {
      return (
        <div className="one_comp" key={i} onClick={() => { props.ChangeStock(listitem.name); props.openList(); }}>
          <div className="name">
            <h1 style={{ fontSize: '20px', fontWeight: "100" }}>{listitem.name}</h1>
            <p style={{ fontSize: '15px' }}>{listitem.companyName}</p>
          </div>
          <div className="name">
            <div className="btn">
              <p style={{ color: 'white', fontSize: '20px', fontWeight: "100" }}>${listitem.latestPrice}</p>
              {
                listitem.change > 0 ?
                  <p style={{ fontSize: '15px', borderRadius: '5px', padding: '2px 8px', margin: '0px', backgroundColor: '#34E36F', color: 'black', display: 'flex', justifyContent: 'center', width: '60px' }}>
                    <ArrowUpwardIcon fontSize='small' />
                    {(listitem.change)}
                  </p>
                  :
                  <p style={{ fontSize: '15px', borderRadius: '5px', padding: '2px 8px', margin: '0px', backgroundColor: '#FF6341', color: 'black', display: 'flex', justifyContent: 'center', width: '60px' }}>
                    <ArrowDownwardIcon fontSize='small' />
                    {(listitem.change)}
                  </p>
              }
            </div>
          </div>
        </div>
      )
    })
  )
}
export default WatchList;