import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "./suggestList.css";
import ExploreIcon from '@mui/icons-material/Explore';


import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import { useAnimation } from 'framer-motion';



function SuggestList(props) {


  const { ref, inView } = useInView({
    threshold: 1
  });
  const animation = useAnimation();
  useEffect(() => {
    if (inView) {
      animation.start({
        x: 0,
        transition: {
          type: 'spring', duration: 1, bounce: 0.2
        }
      })
    }
    if (!inView) {
      animation.start({
        x: '-100vw'
      })
    }
  })

  const list = ["AAPL", "AMZN", "FB", "NFLX", "TSLA", "MSFT", "GOOG", "TWTR"];
  const [stocksData, setStocksData] = useState([]);
  const getCompanyData = (stock, i) => {
    const BASE_URL = 'https://finnhub.io/api/v1/stock/profile2?symbol=';
    const KEY_URL = '&token=c58valiad3idb0atsp6g';
    return axios
      .get(`${BASE_URL}${stock}${KEY_URL}`)
      .catch((error) => {
        console.error("Error", error.message);
      });
  };
  const getMyStocks = () => {
    let promises = [];
    let tempData = [];
    list.map((doc, i) => {
      let temp = ({ name: doc })
      promises.push(getCompanyData(temp.name, i)
        .then(res => {
          tempData.push({
            name: temp.name,
            ...res.data
          })
        })
      )
    })
    Promise.all(promises).then(() => {
      setStocksData(tempData);
    })
  }
  useEffect(() => {
    getMyStocks();
  }, []);
  function update(stock) {

  }
  return (
    <div className="sug_list">
      <p style={{ fontSize: '30px', marginBottom: "20px", color: "white" }} ref={ref}><ExploreIcon /> Discover More</p>
      <div className="sug_list__container">
        {
          stocksData.map((listitem, i) => {
            return (
              <motion.div className="one__sug__container" key={i} animate={animation} onClick={() => { props.ChangeStock(listitem.ticker); props.fieldRef.current.scrollIntoView({ behavior: "smooth", }); }}>
                <div>
                  <p style={{ fontSize: '15px', color: "white", display: 'flex', alignItems: "start" }}>
                    <img src={listitem.logo} height="30px" width="30px" style={{ marginRight: "10px" }} />
                    {listitem.name}
                  </p>
                  <p style={{ fontSize: '15px', color: "white", width: 'min-content', padding: '3px', marginTop: "5px" }}>{listitem.ticker}</p>
                </div>
                <div style={{ display: "flex", alignItems: "center", justifyContent: 'space-between' }}>
                  <p style={{ fontSize: '12px' }}>Industry: {listitem.finnhubIndustry}</p>
                </div>
              </motion.div>
            )
          })
        }
      </div>
    </div>
  )
}
export default SuggestList;