import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Zoom from 'react-reveal/Zoom';

function Info(props) {
  const [stocksData, setStocksData] = useState([]);
  const [desc, setDesc] = useState([]);
  const [bool, setBool] = useState(true)
  useEffect(() => {
    const getStocksData = (stock) => {
      const BASE_URL = 'https://finnhub.io/api/v1/quote?symbol=';
      const KEY_URL = '&token=c58valiad3idb0atsp6g';
      return axios
        .get(`${BASE_URL}${stock}${KEY_URL}`)
        .catch((error) => {
          console.error("Error", error.message);
        });
    };
    const getDesc = (stock) => {
      const BASE_URL = 'https://cloud.iexapis.com/stable/stock/';
      const KEY_URL = '/company?token=pk_8ba887922f7c4eaa84ff14bcf63edcc6 ';
      //pk_4cd102e04d5447289c91ec8bff90a56e
      return axios
        .get(`${BASE_URL}${stock}${KEY_URL}`)
        .catch((error) => {
          console.error("Error", error.message);
        });
    };
    getStocksData(props.currentStock).then(res => { setStocksData(res.data); })
    getDesc(props.currentStock).then(res => { setDesc(res.data); })
  }, [props.currentStock]);
  return (
    <div className="info__container">
      <Zoom left cascade>
        <div>
          <div className='one__stat'>
            <font color='gray'>Open</font>{Number(stocksData['o']).toFixed(2)}
          </div>
          <div className='one__stat'>
            <font color='gray'>low</font>{stocksData['l']}
          </div>
          <div className='one__stat'>
            <font color='gray'>Change</font>{stocksData['d']}
          </div>
        </div>
        <div>
          <div className='one__stat'>
            <font color='gray'>High</font>{stocksData['h']}
          </div>
          <div className='one__stat'>
            <font color='gray'>Prev.Close</font>{stocksData['c']}
          </div>
          <div className='one__stat'>
            <font color='gray'>ChangePr</font>{((stocksData['dp']))}%
          </div>
        </div>
      </Zoom>
    </div>
  )
}
export default Info;