import React, { useState, useEffect, useRef } from 'react';
import EventNoteIcon from '@mui/icons-material/EventNote';
import './NewsFeed.css';
import { db } from './firebase';
import axios from 'axios';
import AddIcon from '@mui/icons-material/Add';
import CheckIcon from '@mui/icons-material/Check';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import InfoIcon from '@mui/icons-material/Info';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import LineChart from './chartjs';
import SuggestList from './suggested';
import Rotate from 'react-reveal/Rotate';
import Roll from 'react-reveal/Roll';
import Swing from 'react-reveal/Swing';



import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import { useAnimation } from 'framer-motion';

import Jump from 'react-reveal/Jump';
function NewsFeed(props) {
  const [stocksData, setStocksData] = useState([]);
  const [companyData, setCompanyData] = useState([]);
  const [news, setNews] = useState([]);
  const [change, setChange] = useState(false);
  const [desc, setDesc] = useState([]);
  const [bool, setBool] = useState(true)

  async function checkIfListed() {
    const watchListRef = db.collection('MyWatchList');
    const check = await watchListRef.where('StockName', '==', props.currentStock).get();
    if (check.empty) {
      setChange(false)
      return;
    }
    else {
      setChange(true)
      return;
    }
  }
  useEffect(() => {
    const getStocksData = (stock) => {
      const BASE_URL = 'https://finnhub.io/api/v1/quote?symbol=';
      const KEY_URL = '&token=c58valiad3idb0atsp6g';
      return axios
        .get(`${BASE_URL}${stock}${KEY_URL}`)
        .catch((error) => {
          console.error("Error", error.message);
        });
    };
    const getCompanyData = (stock) => {
      const BASE_URL = 'https://finnhub.io/api/v1/stock/profile2?symbol=';
      const KEY_URL = '&token=c58valiad3idb0atsp6g';
      return axios
        .get(`${BASE_URL}${stock}${KEY_URL}`)
        .catch((error) => {
          console.error("Error", error.message);
        });
    };
    const getDesc = (stock) => {
      const BASE_URL = 'https://cloud.iexapis.com/stable/stock/';
      const KEY_URL = '/company?token=pk_8ba887922f7c4eaa84ff14bcf63edcc6 ';
      //pk_4cd102e04d5447289c91ec8bff90a56e
      return axios
        .get(`${BASE_URL}${stock}${KEY_URL}`)
        .catch((error) => {
          console.error("Error", error.message);
        });
    };
    getDesc(props.currentStock).then(res => { setDesc(res.data); })
    const fetchNews = async (stock) => {
      let newDate = new Date();
      const prev_date = `${newDate.getFullYear()}-${(newDate.getMonth() < 9 ? '0' + (newDate.getMonth()) : (newDate.getMonth()))}-${(newDate.getDate() < 10 ? '0' + newDate.getDate() : newDate.getDate())}`;
      const current_date = `${newDate.getFullYear()}-${(newDate.getMonth() < 9 ? '0' + (newDate.getMonth() + 1) : (newDate.getMonth() + 1))}-${(newDate.getDate() < 10 ? '0' + newDate.getDate() : newDate.getDate())}`;
      const BASE_URL = 'https://finnhub.io/api/v1/company-news?symbol=';
      const KEY_URL = `from=${prev_date}&to=${current_date}&token=c58valiad3idb0atsp6g`;
      try {
        const response = await fetch(`${BASE_URL}${stock}&${KEY_URL}`);
        const json = await response.json();
        setNews(json);
      } catch (error) {
        console.log(error);
      }
    }
    fetchNews(props.currentStock);
    getStocksData(props.currentStock).then(res => { setStocksData(res.data) })
    getCompanyData(props.currentStock).then(res => { setCompanyData(res.data) })
    console.log(companyData)
    checkIfListed();
  }, [props.currentStock]);
  const Eye = () => {
    function changeVisible() {
      setChange(prev => !prev)
    }
    const addTo = async () => {
      await db.collection('MyWatchList').doc(props.currentStock).set({
        StockName: props.currentStock,
      })
    }
    const removeFrom = async () => {
      await db.collection('MyWatchList').doc(props.currentStock).delete();
    }
    return (
      (change
        ?
        <div className='Follow__container' onClick={
          () => {
            changeVisible();
            removeFrom();
          }
        } style={{ color: '#306EFF', height: 'min-content', marginRight: '10px' }}>
          <CheckIcon />
          <p style={{ fontSize: '20px', padding: '0px', margin: '0px 5px' }}>Following</p>
        </div>
        :
        <div className='Follow__container' onClick={
          () => {
            changeVisible();
            addTo();
          }
        } style={{ backgroundColor: '#306EFF', height: 'min-content', marginRight: '10px' }}>
          <AddIcon />
          <p style={{ fontSize: '20px', margin: '0px 5px' }}>Follow</p>
        </div>
      )
    )
  }
  function handleClick() {
    if (bool) {
      document.getElementsByClassName("desc")[0].style.height = 'max-content';
    }
    else {
      document.getElementsByClassName("desc")[0].style.height = '35px';
    }
    setBool((prev) => !prev);
  }
  console.log(desc)
  return (
    <>
      <div className="newsfeed__chartsection">
        <Jump>
          <div className="info__current">
            <div style={{ fontSize: '1.5em', marginLeft: '20px', marginRight: '20px', marginTop: '20px', display: 'flex', alignItems: 'end', justifyContent: "space-between", width: "98%" }}>
              <div>
                {companyData['ticker']}
                <span onClick={() => window.open(companyData.weburl, "_blank")} style={{ fontSize: '0.7em', padding: '2px 5px', borderRadius: '8px', marginLeft: '5px', color: 'gray', fontWeight: 'initial' }}>
                  {companyData['name']}
                </span>
              </div>
              <div>
                <Swing>
                  <Eye className="eye" />
                </Swing>
              </div>
            </div>
            <div className="stats__follow">
              <div className="companyName__changePr">
                <p style={{ fontSize: '40px', margin: ' 3px 20px', fontWeight: 'light' }}>
                  ${stocksData['c']}
                </p>
                {(Number(stocksData['dp']) > 0 ?
                  <p className="main__changePr__green" style={{ fontSize: '20px', fontWeight: 'light', borderRadius: '5px', padding: '5px 8px', backgroundColor: '#e6f4ea', color: '#137333', display: 'flex', justifyContent: 'center' }}>
                    <ArrowUpwardIcon fontSize='small' />
                    {(stocksData['dp'])}%
                  </p>
                  :
                  <p className="main__changePr__red" style={{ fontSize: '20px', fontWeight: 'light', borderRadius: '5px', padding: '5px 8px', backgroundColor: '#f3cac6', color: '#a50e0e', display: 'flex', justifyContent: 'center' }}>
                    <ArrowDownwardIcon fontSize='small' />
                    {(stocksData['dp'])}%
                  </p>
                )}
              </div>
            </div>
          </div>
        </Jump>
        <div className="newsfeed__chart" style={{ color: 'black' }}>
          <LineChart currentStock={props.currentStock} redOrGreen={Number(stocksData['dp']) > 0 ? ["GreenYellow", "chartreuse"] : ["red", 'OrangeRed']} />
        </div>
        <div className="desc">
          <p style={{ fontSize: '30px', marginBottom: '10px', display: 'flex', alignItems: 'center', color: 'white' }} className='news__heading'>
            <InfoIcon style={{ marginRight: '5px' }} /> About<div style={{ flex: '1' }} />
            <Roll right>
              {(bool ? <ExpandMoreIcon fontSize="large" onClick={() => { handleClick() }} /> : <ExpandLessIcon fontSize="large" onClick={() => { handleClick() }} />)}
            </Roll>
          </p>
          {(Object.keys(desc).length != 0 ?
            <>
              <div style={{ display: 'flex', justifyContent: 'space-between', margin: '10px 0px' }}>
                {desc.companyName}
              </div>
              <p>{desc.description}</p>
              <div>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
                  <font color='gray'>CEO</font>{desc.CEO}
                </div>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
                  <font color='gray'>country</font>{desc.country}
                </div>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
                  <font color='gray'>employees</font>{desc.employees}
                </div>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
                  <font color='gray'>exchange</font>{desc.exchange}
                </div>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '10px' }}>
                  <font color='gray'>sector</font>{desc.sector}
                </div>
              </div>
            </>
            :
            <p>NO DESC</p>)}
        </div>
      </div>
      <div className='bottom__container'>
        <div className="news__container">
          <p style={{ fontSize: '30px', marginBottom: '10px' }} className='news__heading'><EventNoteIcon /> News</p>
          <div className="allNews__container">
            {
              (news.length === 0
                ? <p style={{ fontSize: '20px', margin: ' 3px 0px', fontWeight: 'light' }}>No news to show about {stocksData['companyName']}</p>
                : news.slice(0, 5).map((news, i) => {
                  var event = new Date(Date(news.datetime));
                  let date = JSON.stringify(event)
                  date = date.slice(1, 11)
                  return (
                    <>
                      <div className="oneNews__container" key={i} onClick={() => window.open(news.url, "_blank")}>
                        <div>
                          <p style={{ fontSize: '15px', paddingBottom: '5px' }}>{news.source} • {date}</p>
                          <h1>{news.headline}</h1>
                        </div>
                        <div style={{ marginLeft: '10px' }}>
                          <img src={news.image} height='80px' width='90px' style={{ borderRadius: '10px' }} alt="News" onError={(e) => { e.target.onerror = null; e.target.src = `${companyData.logo}` }}></img>
                        </div>
                      </div>
                    </>
                  )
                })
              )
            }
            <div className="suggestion__container">
              <div >
                <SuggestList ChangeStock={props.ChangeStock} fieldRef={props.fieldRef} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default NewsFeed;