import React from 'react';
import styled from 'styled-components';
import './Stats.css';
import WatchList from './WatchList';
import BookmarkRoundedIcon from '@mui/icons-material/BookmarkRounded';
import AutoGraphIcon from '@mui/icons-material/AutoGraph';
import GppBadIcon from '@mui/icons-material/GppBad';
import ClearRoundedIcon from '@mui/icons-material/ClearRounded';
import TopGainers from './topGainers';
import TopLosers from './topLosers';

import Rotate from 'react-reveal/Rotate';
import Slide from 'react-reveal/Slide';


function Stats(props) {
    const ChangeTheStock = (symbol) => {
        props.ChangeStock(symbol);
    }
    const Background = styled.div`
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba( 0, 0, 0, 0.1);
    backdrop-filter: blur( 25px );
    -webkit-backdrop-filter: blur( 25px );
    border-radius: 10px;
    border-radius: 5px;
    z-index: 100;
`;
    const Close = styled.div`
    width: 50px;
    height: 50px;
    left: 45%;
    top: 0;
    display:flex;
    justify-content:center;
    align-items:center;
    position: fixed;
    background: rgba( 0, 0, 0, 1);
    backdrop-filter: blur( 5.5px );
    -webkit-backdrop-filter: blur( 5.5px );
    border-bottom-left-radius: 100px;
    border-bottom-right-radius: 100px;
    border: 1px solid rgba( 255, 255, 255, 0.10 );
    z-index: 100;
`;
    return (
        <>
            {(window.innerWidth < 600
                ?
                <div style={{ border: 'none' }}>
                    <Close className="glowing">
                        <Slide top>
                            <Rotate>
                                <ClearRoundedIcon fontSize="large" style={{ fill: "white" }} onClick={props.openList} />
                            </Rotate>
                        </Slide>
                    </Close>
                    <Background>
                        <div className="Main__stats__container">
                            <div className="one_comp__container">
                                <div className="one_comp" style={{ display: 'flex', justifyContent: 'space-between' }}>
                                    <div className="header" style={{ display: 'flex', color: "#00BFFF", fontFamily: 'Trebuchet MS, sans-serif' }}>
                                        <h3> My Watchlist</h3>
                                    </div>
                                    <BookmarkRoundedIcon fontSize="medium" style={{ fill: "#00BFFF" }} />
                                </div>
                                <WatchList ChangeStock={ChangeTheStock} openList={props.openList} />
                            </div>
                            <div className="one_comp__container">
                                <div className="one_comp" style={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
                                    <div className="header" style={{ display: 'flex', color: "#FFD700", fontFamily: 'Trebuchet MS, sans-serif' }}>
                                        <h3> Top Gainers</h3>
                                    </div>
                                    <AutoGraphIcon fontSize="medium" style={{ fill: "#FFD700" }} />
                                </div>
                                <TopGainers ChangeStock={ChangeTheStock} openList={props.openList} />
                            </div>
                            <div className="one_comp__container">
                                <div className="one_comp" style={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
                                    <div className="header" style={{ display: 'flex', color: "red", fontFamily: 'Trebuchet MS, sans-serif' }}>
                                        <h3> Top Losers</h3>
                                    </div>
                                    <GppBadIcon fontSize="medium" style={{ fill: "red" }} />
                                </div>
                                <TopLosers ChangeStock={ChangeTheStock} openList={props.openList} />
                            </div>
                        </div>
                    </Background>
                </div>
                :
                <>
                    <div className="Main__stats__container">
                        <div className="one_comp__container">
                            <div className="one_comp" style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <div className="header" style={{ display: 'flex', color: "#00BFFF", padding: '5px' }}>
                                    <h1> My Watchlist</h1>
                                </div>
                                <BookmarkRoundedIcon fontSize="medium" style={{ fill: "#00BFFF" }} />
                            </div>
                            <WatchList ChangeStock={ChangeTheStock} openList={props.openList} />
                        </div>
                        <div className="one_comp__container">
                            <div className="one_comp" style={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
                                <div className="header" style={{ display: 'flex', color: "#FFD700", padding: '5px' }}>
                                    <h1> Top Gainers</h1>
                                </div>
                                <AutoGraphIcon fontSize="medium" style={{ fill: "#FFD700" }} />
                            </div>
                            <TopGainers ChangeStock={ChangeTheStock} openList={props.openList} />
                        </div>
                        <div className="one_comp__container">
                            <div className="one_comp" style={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
                                <div className="header" style={{ display: 'flex', color: "red", padding: '5px' }}>
                                    <h1> Top Losers</h1>
                                </div>
                                <GppBadIcon fontSize="medium" style={{ fill: "red" }} />
                            </div>
                            <TopLosers ChangeStock={ChangeTheStock} openList={props.openList} />
                        </div>
                    </div>
                </>
            )}
        </>
    )
}
export default Stats;