import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './Header.css';
import Autocomplete from '@material-ui/lab/Autocomplete';
import SearchIcon from '@mui/icons-material/Search';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import LogoutIcon from '@mui/icons-material/Logout';
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';

import RubberBand from 'react-reveal/RubberBand';

function Header(props) {
  const [query, setQuery] = useState("");
  const [rec, setRec] = useState([]);
  function handleChange(e) {
    setQuery(e);
  }
  const getStocksData = (stock) => {
    const BASE_URL = 'https://finnhub.io/api/v1/search?q=';
    const KEY_URL = '&token=c4fv34iad3i9aff8eol0';
    return axios
      .get(`${BASE_URL}${stock}${KEY_URL}`)
      .catch((error) => {
        console.error("Error", error.message);
      });
  };
  useEffect(() => {
    getStocksData(query)
      .then((res) => {
        setRec(res.data.result);
      });
  }, [query]);
  return (
    <div className="header__wrapper">
      <div>
        <div className="logo">
          StocksHub
        </div>
        {(window.innerWidth < 600
          ?
          <div className="ham">
            <RubberBand>
              <MenuRoundedIcon fontSize="large" onClick={props.openList} />
            </RubberBand>
          </div>
          :
          <div />
        )}
        <div className="header__searchbar" style={{ position: 'relative', marginLeft: '150px' }}>
          <Autocomplete
            id="combo-box-demo"
            options={rec}
            style={{ margin: '0px', padding: '0px', backgroundColor: 'rgb(36, 36, 36)' }}
            getOptionLabel={(option) => (option.symbol)}
            renderOption={(option) => (
              <React.Fragment>
                <div style={{ justifyContent: 'space-around', color: 'black' }} onClick={() => props.ChangeStock(option.symbol)}><div><h1 style={{ color: 'black' }}>{option.symbol}</h1></div><div><p style={{ color: 'black' }}>{option.description}</p></div></div>
              </React.Fragment>
            )}
            style={{ width: 370 }}
            renderInput={(params) => (
              <div ref={params.InputProps.ref} className="input__container">
                <input type="text" {...params.inputProps} placeholder="Search Symbol..." className="inputBox" value={query} onChange={(e) => handleChange(e.target.value)} />
              </div>
            )}
          />
        </div>
      </div>
      <div className="logOut">
        <LogoutIcon fontSize="large" style={{ fill: 'gray', margin: '0px 20px' }} />
      </div>
    </div>
  )
}

export default Header;