import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
function TopGainers(props) {
  const [stocksData, setStocksData] = useState([]);
  const getStocksData = () => {
    const BASE_URL = 'https://financialmodelingprep.com/api/v3/gainers?apikey=6fe6a1d2be6e2c35aaaf40b2b0e01636';
    //e1fb2985a3f3f07e64085db6c15acd97
    //6fe6a1d2be6e2c35aaaf40b2b0e01636
    return axios
      .get(`${BASE_URL}`)
      .catch((error) => {
        console.error("Error", error.message);
      });
  };
  useEffect(() => {
    getStocksData().then(res => { setStocksData(res.data) })
  }, []);
  return (
    stocksData.slice(0, 3).map((listitem, i) => {
      return (
        <div className="one_comp" key={i} onClick={() => { props.ChangeStock(listitem.name); props.openList(); }}>
          <div className="name">
            <h1 style={{ fontSize: '20px', fontWeight: "100" }}>{listitem.ticker}</h1>
            <p style={{ fontSize: '15px' }}>{listitem.companyName.substring(0, 10)}</p>
          </div>
          <div className="name">
            <div className="btn">
              <p style={{ color: 'white', fontSize: '20px', fontWeight: "100" }}>${listitem.price}</p>
              {
                listitem.changes > 0 ?
                  <p style={{ fontSize: '15px', borderRadius: '5px', padding: '2px 8px', margin: '0px', backgroundColor: '#34E36F', color: 'black', display: 'flex', justifyContent: 'center', width: '60px' }}>
                    <ArrowUpwardIcon fontSize='small' />
                    {(listitem.changesPercentage).slice(0, 5)}%
                  </p>
                  :
                  <p style={{ fontSize: '15px', borderRadius: '5px', padding: '2px 8px', margin: '0px', backgroundColor: '#FF6341', color: 'black', display: 'flex', justifyContent: 'center', width: '60px' }}>
                    <ArrowDownwardIcon fontSize='small' />
                    {(listitem.changesPercentage).slice(0, 5)}%
                  </p>
              }
            </div>
          </div>
        </div>
      )
    })
  )
}
export default TopGainers;